import Global from "./Base/Global";

const { ccclass, property } = cc._decorator;

@ccclass
export default class SoundManager extends cc.Component {
    @property({
        type: cc.AudioClip
    })
    InstSound: cc.AudioClip = null;
    @property({
        type: cc.AudioClip
    })
    VoiceSound: cc.AudioClip = null;
    //
    // @property({
    //     type: cc.AudioClip
    // })
    // round1: cc.AudioClip = null;
    // @property({
    //     type: cc.AudioClip
    // })
    // round2: cc.AudioClip = null;
    // @property({
    //     type: cc.AudioClip
    // })
    // soundDE: cc.AudioClip = null;
    // @property({
    //     type: cc.AudioClip
    // })
    // soundGunAuto: cc.AudioClip = null;
    @property({
        type: cc.AudioClip
    })
    soundMomyLaugh: cc.AudioClip = null;
    @property({
        type: cc.AudioClip
    })
    soundMomyScream: cc.AudioClip = null;
    @property({
        type: cc.AudioClip
    })
    soundHeartBreak: cc.AudioClip = null;
    @property({
        type: cc.AudioClip
    })
    soundDaddyAttack: cc.AudioClip = null;
    @property({
        type: cc.AudioClip
    })
    soundDaddyActive: cc.AudioClip = null;
    @property({
        type: cc.AudioClip
    })
    SpotLight: cc.AudioClip = null;
    PlaySoundGame()
    {
        cc.audioEngine.playEffect(this.InstSound, false);
        cc.audioEngine.playEffect(this.VoiceSound, false);
    }
}
